.container{
    width: 607px;
    height: 680px;
    margin: auto;
    margin-top: 12px;
   margin-bottom: 12px;
    border-radius: 12px;
    background-color: #191970;
}
.top-bar{
    display: flex;
    justify-content: center;
    gap: 14px;
    padding-top: 40px;
}
.top-bar input{
    display: flex;
    width: 362px;
    height: 60px;
    background: white;
    border: none;
    outline: none;
    border-radius: 40px;
    padding-left: 40px;
    color: grey;
    font-size: 40px;
    font-weight: 400;
}
.search-icon{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 78px;
    height: 60px;
    background: white;
    border-radius: 40px;
    cursor: pointer;
}
.weather-image{
    margin-top: 10px;
    display: flex;
    justify-content: center;
}  
.weather-temp{
    display: flex;
    justify-content: center;
    color: white;
    font-size: 70px;
    font-weight: 200;
}
.weather-location{
    display: flex;
    justify-content: center;
    color: white;
    font-size: 60px;
    font-weight: 400;
}
.data-container{
    margin-top: 50px;
    color: white;
    display: flex;
    justify-content: center;
}
.element{
    margin: auto;
    display: flex;
    align-items: flex-start;
    gap: 12px;
}
.data{
    font-size: 30px;
    font-weight: 400;
}
.text{
    font-size: 20px;
    font-weight: 400;
}
.icon{
    margin-top: 10px;
}











